<template lang="html">
  <div class="main">
    <el-row :gutter="20">
      <!---->
      <el-col :span="8">
        <div class="grid-content">
          <div id="chart-page"></div>
        </div>
      </el-col>
      <!---->
      <el-col :span="8">
        <div class="grid-content">
          <div id="line-chart"></div>
        </div>
      </el-col>
      <el-col :span="8"><div class="grid-content"></div></el-col>
    </el-row>

    <el-row :gutter="20">
      <!---->
      <el-col :span="8">
        <div class="grid-content">
          <div id="chart-pie"></div>
        </div>
      </el-col>
      <!---->
      <el-col :span="8">
        <div class="grid-content">
          <div id="funnel-chart"></div>
        </div>
      </el-col>
      <el-col :span="8"><div class="grid-content"></div></el-col>
    </el-row>

  </div>
</template>

<script>
import echarts from 'echarts';
export default {
  data() {
    return {
    };
  },
  mounted: () =>{
    // 
    let el_histogram = document.getElementById('chart-page');
    echarts.init(el_histogram).setOption({
      title: { text: '' },
      tooltip: {},
      xAxis: {
        data: ['', '', '', '', '', '', '']
      },
      yAxis: {},
      series: [{
        name: '',
        type: 'bar',
        data: [5, 20, 36, 10, 10, 20, 30]
      }]
    });

    //  
    let el_lineChart = document.getElementById('line-chart');
    echarts.init(el_lineChart).setOption({
      title: {
        text: '',
        subtext: ''
      },
      tooltip: {
        trigger: 'axis'
      },
      legend: {
        data: ['', '', '']
      },
      toolbox: {
        show: true,
        feature: {
          magicType: {show: true, type: ['stack', 'tiled']},
          saveAsImage: {show: true}
        }
      },
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data: ['', '', '', '', '', '', '']
      },
      yAxis: {
        type: 'value'
      },
      series: [{
        name: '',
        type: 'line',
        smooth: true,
        data: [10, 12, 21, 54, 260, 830, 710]
      }, {
        name: '',
        type: 'line',
        smooth: true,
        data: [30, 182, 434, 791, 390, 30, 10]
      }, {
        name: '',
        type: 'line',
        smooth: true,
        data: [1320, 1132, 601, 234, 120, 90, 20]
      }]
    });

    //  
    let el_chart = document.getElementById('chart-pie');
    echarts.init(el_chart).setOption({
      backgroundColor: '#2c343c',
      title: {
        text: '',
        // left: 'center',
        top: 20,
        textStyle: {
          color: '#ccc'
        }
      },
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b} : {c} ({d}%)'
      },
      visualMap: {
        show: false,
        min: 80,
        max: 600,
        inRange: {
          colorLightness: [0, 1]
        }
      },
      series: [
        {
          name: '',
          type: 'pie',
          radius: '55%',
          center: ['50%', '50%'],
          data: [
            {value: 335, name: ''},
            {value: 310, name: ''},
            {value: 274, name: ''},
            {value: 235, name: ''},
            {value: 400, name: ''}
          ].sort((a, b) => {
            return a.value - b.value;
          }),
          roseType: 'angle',
          label: {
            normal: {
              textStyle: {
                color: 'rgba(255, 255, 255, 0.3)'
              }
            }
          },
          labelLine: {
            normal: {
              lineStyle: {
                color: 'rgba(255, 255, 255, 0.3)'
              },
              smooth: 0.2,
              length: 10,
              length2: 20
            }
          },
          itemStyle: {
            normal: {
              color: '#c23531',
              shadowBlur: 200,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          },
          animationType: 'scale',
          animationEasing: 'elasticOut',
          animationDelay: function(idx) {
            return Math.random() * 200;
          }
        }
      ]
    });

    //  
    let el_funnel = document.getElementById('funnel-chart');
    echarts.init(el_funnel).setOption({
      title: {
        text: '',
        subtext: ''
      },
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b} : {c}%'
      },
      toolbox: {
        feature: {
          dataView: {readOnly: false},
          restore: {},
          saveAsImage: {}
        }
      },
      legend: {
        data: ['', '', '', '', '']
      },
      calculable: true,
      series: [
        {
          name: '',
          type: 'funnel',
          left: '10%',
          top: 60,
          // x2: 80,
          bottom: 60,
          width: '80%',
          // height: {totalHeight} - y - y2,
          min: 0,
          max: 100,
          minSize: '0%',
          maxSize: '100%',
          sort: 'descending',
          gap: 2,
          label: {
            normal: {
              show: true,
              position: 'inside'
            },
            emphasis: {
              textStyle: {
                fontSize: 20
              }
            }
          },
          labelLine: {
            normal: {
              length: 10,
              lineStyle: {
                width: 1,
                type: 'solid'
              }
            }
          },
          itemStyle: {
            normal: {
              borderColor: '#fff',
              borderWidth: 1
            }
          },
          data: [
            {value: 60, name: ''},
            {value: 40, name: ''},
            {value: 20, name: ''},
            {value: 80, name: ''},
            {value: 100, name: ''}
          ]
        }
      ]
    });

  },
  methods: {}
};
</script>

<style lang="scss" scoped>
  .el-row {
    margin-bottom: 25px;
  }
  #chart-page, #line-chart, #funnel-chart, #chart-pie {
    height: 350px;
  }
</style>



